* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Nunito, sans-serif;
}

main, header {
  --background-color: white;
  --text-color: black;
  --elements-color: white;
  padding: 24px;
}

main {
  background-color: var(--background-color);
  color: var(--text-color);
  min-height: 100vh;
}

main.dark, header.dark {
  --background-color: #202c37;
  --text-color: white;
  --elements-color: #2b3945;
}

a {
  color: inherit;
  text-decoration-line: none;
}

.header-container {
  background-color: var(--elements-color);
  z-index: 1;
  padding-inline: 24px;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 4px #0000001a;
}

.header-content {
  max-width: 1200px;
  justify-content: space-between;
  align-items: center;
  margin-inline: auto;
  display: flex;
}

.title {
  font-weight: 800;
}

.theme-changer {
  cursor: pointer;
}

.countries-container {
  max-width: 1200px;
  margin-top: 16px;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 64px;
  margin-inline: auto;
  display: flex;
}

.country-card {
  width: 250px;
  background-color: var(--elements-color);
  border-radius: 8px;
  padding-bottom: 24px;
  transition: all .2s ease-in-out;
  display: inline-block;
  overflow: hidden;
  box-shadow: 0 0 8px #0003;
}

.country-card:hover {
  transform: scale(1.03);
  box-shadow: 0 0 16px #0003;
}

.card-title {
  margin-block: 16px;
  font-size: 24px;
}

.card-text {
  padding-inline: 16px;
}

.card-text p {
  margin-block: 8px;
}

.country-card img {
  width: 100%;
}

.search-filter-container {
  max-width: 1200px;
  margin-inline: auto;
  justify-content: space-between;
  gap: 24px;
  margin-top: 16px;
  margin-bottom: 48px;
  display: flex;
}

.search-container {
  max-width: 350px;
  width: 100%;
  color: #999;
  background-color: var(--elements-color);
  color: var(--text-color);
  border-radius: 4px;
  padding-left: 24px;
  overflow: hidden;
  box-shadow: 0 0 8px #00000026;
}

.search-container input {
  width: 90%;
  background-color: inherit;
  color: inherit;
  border: none;
  outline: none;
  padding: 16px;
}

.search-container input::placeholder {
  color: #999;
}

.filter-by-region {
  width: 250px;
  background-color: var(--elements-color);
  color: var(--text-color);
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 16px;
  box-shadow: 0 0 8px #00000026;
}

@media (max-width: 768px) {
  .countries-container {
    justify-content: center;
  }

  .title {
    font-size: 16px;
  }

  .header-content p {
    font-size: 12px;
  }

  .search-filter-container {
    flex-direction: column;
  }
}

.countries-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
  padding: 20px;
  display: flex;
}

.shimmer {
  width: 250px;
  height: 300px;
  background: linear-gradient(90deg, #e0e0e0 25%, #f0f0f0 50%, #e0e0e0 75%) 0 0 / 200% 100%;
  border-radius: 10px;
  animation: 1.5s linear infinite shimmer-animation;
}

.country-name {
  width: 20px;
  height: 20px;
}

@keyframes shimmer-animation {
  0% {
    background-position: -200% 0;
  }

  100% {
    background-position: 200% 0;
  }
}

.card:hover {
  background-color: #e3f2f8c9;
  transform: scale(1.05);
  box-shadow: 0 0 16px 16px #0003;
}

@media (max-width: 670px) {
  .card-container {
    justify-content: center;
  }

  .search-division {
    flex-direction: column;
  }
}

.country-details-container {
  max-width: 1200px;
  margin-inline: auto;
  margin-top: 32px;
}

.back-button {
  cursor: pointer;
  border-radius: 4px;
  padding: 6px 24px;
  box-shadow: 0 0 4px #0003;
}

.country-details {
  align-items: center;
  gap: 64px;
  margin-top: 64px;
  display: flex;
}

.country-details img {
  width: 40%;
  border: 1px solid #ddd;
}

.details-text {
  max-height: 180px;
  flex-flow: column wrap;
  gap: 12px 64px;
  display: flex;
}

.details-text p {
  margin: 0;
}

.border-countries {
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 8px;
  margin-top: 48px;
  display: flex;
}

.border-countries a {
  border-radius: 4px;
  padding: 4px 16px;
  font-size: 14px;
  box-shadow: 0 0 4px #0003;
}

@media (max-width: 940px) {
  .country-details {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    margin-top: 32px;
  }

  .details-text {
    max-height: unset;
  }

  .country-details img {
    width: 100%;
    max-width: 400px;
  }
}

